@import "variables";

@font-face {
    font-family: "ardarling";
    src: url("../../fonts/ardarling.eot");
    src: url("../../fonts/ardarling.eot#iefix") format("embedded-opentype"), url("../../fonts/ardarling.woff") format("woff"), url("../../fonts/ardarling.ttf") format("truetype"), url("../../fonts/ardarling.svg") format("svg");
    font-weight: normal;
    font-style: normal;
}

body {
    padding-top: 70px;
    margin-top: 70px;
}

.header-spacer {
    margin-top: 70px;
}

.submit {
    margin-right: 10px;
}

.btn-links {
    display: inline-block;

    a {
        padding: 6px 2px;
    }
}

nav.navbar {
    background: url("../../img/nav-bg.jpg");
    border: 0;
    border-radius: 0;
    padding: 10px;
    -webkit-box-shadow: 0 1px 5px 0 rgba(0,0,0,0.75);
    -moz-box-shadow: 0 1px 5px 0 rgba(0,0,0,0.75);
    box-shadow: 0 1px 5px 0 rgba(0,0,0,0.75);

    .navbar-collapse[aria-expanded=true],
    .navbar-collapse.collapsing {
        border: 0;
        box-shadow: none;
    }

    .navbar-nav {
        padding-top: 50px;
    }

    .navbar-brand {
        height: auto;

        .logo {
            position: absolute;
            top: 10px;
            height: 130%;
            max-height: 80px;
        }
    }

    .navbar-nav > li > a {
        color: #808080;
        -webkit-transition: all 0.4s ease;
        -moz-transition: all 0.4s ease;
        -ms-transition: all 0.4s ease;
        -o-transition: all 0.4s ease;
        transition: all 0.4s ease;
        padding: 10px 15px;
        text-transform: uppercase;

        &.active {
            background-color: #937c4b;
            color: #000;
        }

        &.strong {
            color: #ffdc93 !important;
        }

        &:hover {
            background-color: #937c4b;
            color: #000;
        }
    }

    .navbar-toggle {
        border-color: #937c4b;

        &:focus {
            background-color: transparent;
        }

        &:hover {
            background-color: #937c4b;
        }

        .icon-bar {
            background-color: #ffdc93;
        }
    }
}

@-webkit-keyframes fadeInRight {
    0% {
        opacity: 0;
        -webkit-transform: translate3d(100%,0,0);
        transform: translate3d(100%,0,0);
    }
    100% {
        opacity: 1;
        -webkit-transform: none;
        transform: none;
    }
}

@keyframes fadeInRight {
    0% {
        opacity: 0;
        -webkit-transform: translate3d(100%,0,0);
        transform: translate3d(100%,0,0);
    }
    100% {
        opacity: 1;
        -webkit-transform: none;
        transform: none;
    }
}

section.banner {
    background-image: url("../../img/banner.jpg");
    background-size: cover;
    background-position: center center;
    padding-top: 69px;
    padding-bottom: 45px;
    margin-top: -70px;

    h1 {
        font-family: "myriadpro-regular";
        font-weight: normal;
        color: #fff;
        margin: 0 0 30px;
        font-size: 32px;
        -webkit-animation-duration: 1s;
        animation-duration: 1s;
        -webkit-animation-fill-mode: both;
        animation-fill-mode: both;
        -webkit-animation-name: fadeInRight;
        animation-name: fadeInRight;
        -webkit-animation-delay: 0.3s;
        animation-delay: 0.3s;

        span {
            background-color: rgba(0, 0, 0, 0.32);
            padding: 15px 15px 10px;
            margin-bottom: 5px;
            display: inline-block;
        }
    }

    h6 {
        font-family: "myriadpro-regular";
        font-size: 24px;
        font-weight: normal;
        color: #fff;
        margin: 0 0 50px;
        line-height: 1.5;
    }

    p {
        font-family: "myriadpro-regular";
        font-size: 14px;
        font-weight: normal;
        color: #101319;
        line-height: 1.6;
        margin: 0 0 25px;
        background-color: rgba(255, 255, 255, 0.6);
        padding: 10px;
    }
}

section.login {
    background-color: #000;
    border-bottom: 1px solid #191714;
    -webkit-box-shadow: 0 3px 10px 1px rgba(0,0,0,1);
    -moz-box-shadow: 0 3px 10px 1px rgba(0,0,0,1);
    box-shadow: 0 3px 10px 1px rgba(0,0,0,1);
    z-index: 1;
    position: relative;
    padding: 15px 0;

    .info {
        background: url("../../img/badge.png");
        min-height: 85px;
        background-repeat: no-repeat;
        text-align: center;
        padding-left: 85px;

        h5 {
            font-family: "myriadpro-bold";
            font-size: 18px;
            color: #b2a27f;
            border-bottom: 1px solid #252525;
            padding-bottom: 5px;
            margin: 0 0 10px;
            padding-top: 13px;

            span {
                color: #ffdc91;
            }
        }

        p {
            font-family: "myriadpro-regular";
            font-size: 12px;
            color: #9b9b9b;
            margin-bottom: 0;
        }
    }

    .form {
        margin-top: 30px;

        form {

            div {
                margin-bottom: 5px;
            }
        }

        .more {
            text-align: right;
        }
    }
}

section.benefits {
    background: url("../../img/benefits-bg.png");
    background-repeat: repeat-x;
    background-size: auto 100%;
    background-position: center center;
    padding: 30px 0 60px;
    position: relative;
    z-index: 1;

    .row > div {
        margin-bottom: 30px;
    }

    h2 {
        font-family: "ardarling";
        color: #b2a27f;
        font-size: 24px;
        text-shadow: 1px 3px 0 rgba(4, 4, 4, 1);
        margin-bottom: 30px;
    }

    p {
        font-size: 13px;
        color: #cfcfcf;
        margin: 0;
        line-height: 2;
    }
}

section.join {
    background-image: url("../../img/joinnow-bg.jpg");
    background-size: auto 180%;
    background-position: center;
    padding-top: 100px;
    margin-top: -100px;

    .header {
        color: #fff;
        font-family: "myriadpro-regular";
        font-weight: normal;
        text-align: center;
        padding: 70px 5% 100px;
        font-size: 24px;
    }

    .register {
        text-align: center;
        margin-bottom: 100px;

        img {
            margin-bottom: 30px;
            max-width: 100%;
        }

        p {
            color: #fff;
            padding: 0 10%;
            margin-bottom: 30px;
        }

        form {
            text-align: left;
            padding: 0 20%;
            color: #fff;

            input[type=checkbox] {
                margin-right: 10px;
            }
        }
    }
}

footer.footer {
    background-image: url("../../img/footer-bg.jpg");
    background-size: cover;
    background-position: center center;
    padding: 30px 0;
    text-align: center;

    ul {
        list-style-type: none;
        padding: 0;

        li {
            padding: 10px;
            display: inline-block;
            margin-top: 5px;
        }

        a {
            color: #fff;
        }
    }
}

@media (min-width: $screen-xs) {

    section.banner {

        h1 {
            font-size: 42px;
        }
    }
}

@media (min-width: $screen-sm) {

    nav.navbar {

        .navbar-nav {
            padding-top: 60px;
        }

        .navbar-brand {

            .logo {
                max-height: 90px;
            }
        }
    }

    section.banner {

        h1 {
            font-size: 52px;
        }
    }

    section.benefits {

        .row > div {
            padding: 25px;
            border-right: 1px solid #000;

            &::after {
                content: "";
                position: absolute;
                z-index: 1;
                border-right: 1px solid #262626;
                display: block;
                top: 0;
                right: 0;
                height: 100%;
            }
        }

        .row > div:last-child {
            border-right-width: 0;

            &::after {
                display: none;
            }
        }

        .row > div:nth-child(2) {
            border-right-width: 0;

            &::after {
                display: none;
            }
        }
    }
}

@media (min-width: $screen-md) {
    body {
        padding-top: 80px;
    }

    nav.navbar {
        padding: 20px 10px;

        .navbar-nav {
            padding-top: 0;
        }

        .navbar-brand {

            .logo {
                max-height: 120px;
            }
        }
    }

    section.banner {

        h1 {
            font-size: 62px;
        }

        p {
            padding-left: 75px;
            background-color: inherit;
        }
    }

    section.login {

        .form {
            margin-top: 10px;

            form div {
                padding: 0 10px;
            }
        }
    }

    section.benefits {
        padding: 40px 0 70px;

        .row {
            display: table;

            & > div {
                margin: 0;
                float: none;
                display: table-cell;
                vertical-align: top;
            }
        }

        .row > div:nth-child(2) {
            border-right-width: 1px;

            &::after {
                display: block;
            }
        }
    }
}

@media (min-width: $screen-lg) {
    body {
        padding-top: 100px;
    }

    nav.navbar {
        padding: 30px 10px;
    }

    section.banner {

        p {
            padding-left: 50px;
        }
    }

    section.join {
        background-size: cover;
    }
}
